@import '../../styles/settings.scss';

.card {
    position: relative;
    background: $color-light;
    border: 1px solid lighten($color-dark, 60%);
    padding: $base-size;
    margin: 0 0 $base-size 0;
    border-radius: $radius;
    display: flex;
    justify-content: space-between;
    font-weight: bold;
}

.star_btn {
    background: $color-success;
    border: 0;
    color: $color-light;
    text-align: center;
    line-height: 1.3;
    padding: $base-size;
    margin: 0 $base-size $base-size $base-size;
    border-radius: $radius;
    cursor: pointer;
    font-weight: 700;
    text-transform: uppercase;
    transition: $transition;

    &:hover {
        background: darken($color-success, 5%);
    }

    &.active {
        color: $color-warning;
        background: $color-success;

    }

    &.transparent {
        background: transparent;

        &:hover {
            color: darken($color-light, 20%);
        }
    }
}
.bin_btn {
    background: $color-success;
    border: 0;
    color: $color-dark;
    text-align: center;
    line-height: 1.3;
    padding: $base-size;
    margin: 0 $base-size $base-size $base-size;
    border-radius: $radius;
    cursor: pointer;
    font-weight: 700;


    &:hover {
        background: darken($color-success, 5%);
    }
}